import React, { useEffect, useState } from "react";
import { Container, Row, Col, Button, Card } from "react-bootstrap";
import "./dashboard.css";
import { MdCallSplit, MdSmartDisplay } from "react-icons/md";
import { FaPlay } from "react-icons/fa";
import { Avatar } from "@mui/material";
import profilepic from "../../asset/profilePicture.png";
import axiosMain from "../utils/axiosMain";


function Dashboard() {
  const [data, setData] = useState({
    totalLead: 0,
    totalInquiry: 0,
    totalEmailSend: 0,
    totalWhatsAppMessageSend: 0,
    totalConnect: 0,
    totalReferral: 0,
  });

  function calculatePercentage(data) {
    const total = data.reduce((sum, item) => sum + item.value, 0);

    return data.map((item) => ({
      ...item,
      percentage:
        total > 0 ? ((item.value / total) * 100).toFixed(2) + "%" : "0%",
    }));
  }
  const connections = Array(8).fill({
    name: "Rubik Sans",
    company: "Company Name",
  });

  useEffect(() => {
    axiosMain
      .get("/data-category")
      .then((res) => {
        if (res.success) {
          setData({
            ...data,
            totalInquiry: calculatePercentage(res.dataCategories.length),
          });
        }
      })
      .catch((err) => console.log(err));
  }, []);

  return (
    <Container fluid className="dashboard">
      {/* Header Section */}
      <Row className="align-items-center mb-4">
        <Col lg={8} sm={12}>
          <h2 className="day">Today</h2>
          <p className="date-time">Mon 22, 2021 | 10:00 AM</p>
        </Col>
        <Col  lg={4} sm={12}>
          <div className="product-tour ms-auto">
            <div>
              <h2>Product Tour</h2>
            </div>
            <div>
              <button className="btn">
                {" "}
                <div style={{ color: "#000000" }}>
                  <FaPlay />
                </div>
              </button>
            </div>
          </div>
        </Col>
      </Row>
      <Row className="mb-4">
        <Col xs={12} sm={12} md={7} lg={5}>
          {/* Stats Cards */}
          <Row className="g-3">
            {[
              { title: "Total Lead", value: data.totalLead },
              { title: "Total Inquiry", value: data.totalInquiry },
              {
                title: "Total Email Send",
                value: data.totalEmailSend,
              },
              {
                title: "Total WhatsApp Message Send",
                value: data.totalWhatsAppMessageSend,
              },
              { title: "Total Connect", value: data.totalConnect },
              { title: "Total Referral", value: data.totalReferral },
            ].map((ele, index) => (
              <Col key={index} xs={12} sm={12} md={12} lg={6}>
                <Card className="stat-card">
                  <Card.Body
                    className="d-flex"
                    style={{
                      flexDirection: "column",
                      justifyContent: "space-between",
                    }}
                  >
                    <Card.Title>{ele.title}</Card.Title>
                    <div
                      className="d-flex"
                      style={{
                        justifyContent: "space-between",
                        alignItems: "center",
                        flexDirection: "row",
                      }}
                    >
                      <h2 className="stat-value">{ele.value}%</h2>
                      <div className="arrow-icon-container">
                        <div className="arrow-icon">
                          <MdCallSplit />
                        </div>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </Col>
            ))}
          </Row>
        </Col>
        <Col xs={12} sm={12} md={7} lg={7}>
          {/* Business Connection Section */}

          <Card className="business-connection">
            <Card.Header className="d-flex justify-content-between business-connection-header">
              <span className="business-connection-title">
                Business Connection
              </span>
              <span>222</span>
            </Card.Header>
            <Card.Body>
              {connections.map((conn, index) => (
                <div className="connection-card" key={index}>
                  <Avatar src={profilepic} alt="CN" />
                  <div className="flex-grow-1 connection-card-user">
                    <p className="mb-0 fw-bold">{conn.name}</p>
                    <small>{conn.company}</small>
                  </div>
                  <button className="connect-btn me-2">Connect</button>
                  <button className="remove-btn">Remove</button>
                </div>
              ))}
            </Card.Body>
          </Card>
        </Col>
      </Row>
    </Container>
  );
}

export default Dashboard;
